/* Basic reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Arial, sans-serif;
  background-color: #f4f4f9;
  color: #333;
}

/* Home container */
.home-container {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

/* Menu styles */
.menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #333;
  padding: 15px 30px;
  color: white;
  position: sticky;
  top: 0;
  z-index: 100;
}

.menu.open .menu-items {
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 60px;
  left: 0;
  width: 100%;
  background-color: #333;
  padding: 20px 0;
}

.menu .logo-title {
  display: flex;
  align-items: center;
}

.logo {
  width: 40px;
  height: auto;
  margin-right: 15px;
}

.menu h1 {
  font-size: 1.5rem;
  font-weight: bold;
  color: white;
}

/* Menu items */
.menu-items {
  display: flex;
  list-style-type: none;
  justify-content: space-between;
  flex-grow: 1;
}

.menu-items li {
  margin-right: 20px;
}

.menu-items li a {
  color: white;
  text-decoration: none;
  padding: 10px 20px;
  font-size: 1rem;
}

.menu-items li a:hover {
  background-color: #555;
}

/* Hamburger icon */
.hamburger {
  display: none;
  font-size: 30px;
  cursor: pointer;
  color: white;
}

/* Media Queries: When the screen is smaller than 768px, show the hamburger menu */
@media (max-width: 768px) {
  .menu-items {
    display: none;
    width: 100%;
    flex-direction: column;
    padding: 20px 0;
    position: absolute;
    top: 60px;
    left: 0;
  }

  .hamburger {
    display: block;
  }

  .menu-items.open {
    display: flex;
  }
}
